




































































html {
  height: 100%;
}

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  font: 14px/1.5 "PingFang SC", Helvetica, "Helvetica Neue", "微软雅黑", Tahoma, Arial, sans-serif;
  background:#e8e8e8;
}

#app {
  color: #2c3e50;
  margin-top: -100px;
  max-width: 600px;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  text-align: center;
}

#app a {
  color: #42b983;
  text-decoration: none;
}

.logo {
  width: 100px;
  height: 100px
}
.red{
  color:red;
}
#todolist li.on{
  opacity: 1;
}
#todolist,#todolist li{
  padding:0;
  list-style: none;
}
#todolist li{
  line-height: 35px;
  background-color: #52b983;
  font-size: 14px;
  margin-bottom: 2px;
  color: #fff;
  opacity: 0.5;
  position: relative;
}
#newItemInput{
  width: 250px;
  height:45px;
  border:2px solid rgba(66, 185,134, .5);
  outline: none;
  font-size: 18px;
  text-align: center;
  background: transparent;
  background-origin: padding-box;
}
span.delete{
  color:red;
  position: absolute;
  right: 10px;
  cursor: pointer;
  font-size: 24px; 
}



